import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./Register.css";
import VisibilityOutlinedIcon from "@mui/icons-material/VisibilityOutlined";
import VisibilityOffOutlinedIcon from "@mui/icons-material/VisibilityOffOutlined";

import alertify from "alertifyjs";
import "alertifyjs/build/css/alertify.css";
import UserAPI from "../../components/api/userAPI";
import { Close } from "@mui/icons-material";

const Register = () => {
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");

  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);

  const [error, setError] = useState("");

  const navigate = useNavigate();

  function validateEmail(email) {
    const re =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  function validatePhone(phone) {
    const phoneNumberRegex = /^[0-9]{10}$/; // Kim tra xem chui c 10 ch s hay khng
    return phoneNumberRegex.test(phone);
  }

  const handleRegister = () => {
    // Handle registration logic here (e.g., API calls, validation)
    if (name.length === 0) {
      setError("Vui lng nhp tn");
    } else if (!validatePhone(phone)) {
      setError("S in thoi khng ng");
    } else if (!validateEmail(email) || email === "") {
      setError("Email khng ng");
    } else if (password.length < 6) {
      setError("Mt khu phi c t nht 6 k t");
    } else if (password !== confirmPassword) {
      setError("Mt khu nhp li khng chnh xc");
    } else {
      setError("");
      const postRegister = async () => {
        const body = {
          name: name,
          phone: phone,
          email: email,
          password: password,
          password_confirmation: confirmPassword,
        };
        try {
          const res = await UserAPI.postRegister(body);
          // alert("ng k thnh cng, bn s c chuyn n trang ch");
          alertify.set("notifier", "position", "bottom-left");
          alertify.success("ng k ti khon thnh cng!");
          console.log(res.data);
          navigate("/login");
        } catch (error) {
          setError(error.response.data.errors[0].message);
        }
      };
      postRegister();
    }
  };

  return (
    <div className="registration-form">
      <Link to="/">
        <Close className="close" />
      </Link>
      <h2>ng k ti khon</h2>
      <h3>ng k  mua v theo di qu trnh c sch</h3>
      <input
        type="text"
        placeholder="H v tn"
        value={name}
        onChange={(e) => setName(e.target.value)}
      />
      <input
        type="text"
        placeholder="S in thoi"
        value={phone}
        onChange={(e) => setPhone(e.target.value)}
      />
      <input
        type="email"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />
      <div className="input-with-eye">
        <input
          type={showPassword ? "text" : "password"}
          placeholder="Mt khu"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <span
          className="eye-icon"
          onClick={() => setShowPassword(!showPassword)}
        >
          {showPassword ? (
            <VisibilityOffOutlinedIcon style={{ color: "black" }} />
          ) : (
            <VisibilityOutlinedIcon style={{ color: "black" }} />
          )}
        </span>
      </div>
      <div className="input-with-eye">
        <input
          type={showPassword ? "text" : "password"}
          placeholder="Nhp li mt khu"
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
        />
        <span
          className="eye-icon"
          onClick={() => setShowPassword(!showPassword)}
        >
          {showPassword ? (
            <VisibilityOffOutlinedIcon style={{ color: "black" }} />
          ) : (
            <VisibilityOutlinedIcon style={{ color: "black" }} />
          )}
        </span>
      </div>
      {error !== "" && <p style={{ color: "red" }}>{error}</p>}
      <button onClick={handleRegister}>ng k</button>
      <p>
        Bn  c ti khon? <a href="/login">ng nhp ngay</a>
      </p>
    </div>
  );
};

export default Register;
